import React from "react"
import Anime from 'react-anime'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from "react-bootstrap"
import "../components/app.css"
import Layout from "../components/layout.js"
import SEO from "../components/seo"
import ProjectNav from "../components/project-nav"

const ComyootPage = () => {
  const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "home/comyoot.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageTwo: file(relativePath: { eq: "comyoot/features-page-2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageThree: file(relativePath: { eq: "comyoot/features-page-3.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFour: file(relativePath: { eq: "comyoot/landing.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      imageFive: file(relativePath: { eq: "comyoot/about.png" }) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
  <Layout>
    <Container fluid={true}>
      <Row>
        <ProjectNav 
          title="ComYoot — UX/UI Design + Web Developer Intern"
          info="Fall 2017 | Pittsburgh, PA"
          skills="User research, Wireframing, Web development, User Testing, Adobe Photoshop, HTML/CSS/Javascript, Bootstrap, Foundation Emails"
          left="/flexable/"
          right="/sketches/"
        >
        </ProjectNav>
        <Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 250}>
        <Col xs={12} sm={12} md={12} lg={{span:8, offset:4}} xl={{span:8, offset:4}} className="project-content">
          <Img fluid={data.imageOne.childImageSharp.fluid} alt="project cover"/>
          <br /><br />
          <h2 className="project-h2">Background</h2>
          <p><a href="http://comyoot.co/" className="link">ComYoot</a> is a mobile app that helps users easily share their skills, services and job opportunities within their community. I served as a UX design and Web Developer intern, working on a full-stack design project that involved re-designing ComYoot's website.</p>
          <br />
          <h2 className="project-h2">Initial Research</h2>
          <p>I analyzed the previous version of the ComYoot website and its competitors to better understand how others were promoting their apps via their websites, taking note on the following:</p>
          <ul>
            <li><b>Overall UI Design:</b> Took into account colors, organization of content, visuals (icons, logo, images, etc.), and other elements.</li><br />
            <li><b>Navigation:</b> Analyzed the navigation that each website had. Many startups have the one-page website layout broken up into different sections (About Us, How The App Works, etc.). Some provide a navigational bar with links that scroll to the section when clicked while others have no navigation.</li><br />
            <li><b>Content Hierarchy:</b> Examined how each company organized, grouped and prioritized their content.</li><br />
            <li><b>Content Relevance:</b> Took note of what kind of content each company provided.</li><br />
            <li><b>Responsiveness:</b> Inspected if each website was mobile friendly.</li>
          </ul>
          <br />
          <h2 className="project-h2">Research and Synthesis</h2>
          <p>I conducted around 30 user studies with a set of questions that gave insight in how users generally interact with a website and how users felt about the previous ComYoot website.</p>
          <p>One of the problems that participants commented on the most was that they had no idea what the website was for when they first viewed the landing page. Another common response was that people found the text illegible at certain sections of the website due to lack of color contrast between the text and background.</p>
          <p>Many participants mentioned that they like to see screenshots of the actual app. Seeing how the app works through these screenshots shows them its usefulness and main functions. Another common response was that they appreciated seeing reviews of the app from real people because they help establish trust in the app.</p>
          <p>I developed several insights from the user studies. Here are a few examples:
            <ul>
              <li>Viewers need to see why this app will replace their current actions.</li>
              <li>Visitors prefer to see the app's interface to understand the features.</li>
            </ul>
          </p>
          <br />
          <h2 className="project-h2">Website Mockups and Designs</h2>
          <p>I created a few mockups for some of the sections in the website to show the general direction I wanted to take when developing the website. The following mockups are for the "Why ComYoot" section:</p>
          <Img fluid={data.imageTwo.childImageSharp.fluid} alt="two iphones mockups"/><br />
          <Img fluid={data.imageThree.childImageSharp.fluid} alt="two iphones connecting"/><br />
          <br />
          <h2 className="project-h2">Website Development</h2>
          <p>I coded the website using basic HTML/CSS/Javascript along with the Bootstrap framework. Below are screenshots of the website; you can also view the live site <a href="http://comyoot.co/" target="_blank" className="link">here</a>.</p>
          <Img fluid={data.imageFour.childImageSharp.fluid} alt="screenshot of landing page"/><br />
          <Img fluid={data.imageFive.childImageSharp.fluid} alt="screenshot of about page"/><br />
          <br />
          <h2 className="project-h2">Further Responsibilities</h2>
          <p>Throughout my internship, I also helped create marketing materials, coded email campaigns (using <a href="https://foundation.zurb.com/emails.html" target="_blank" className="link">Foundation's email framework</a>), and conducted user testing for the app itself. Feel free to <a href="mailto:cchang2@alumni.cmu.edu" className="link">contact me</a> to learn more about my other responsibilities!</p>
          <p></p>
        </Col>
        </Anime>
      </Row>
    </Container>
  </Layout>
  )
}

export default ComyootPage
